import React from "react";
import { ErrorMessage, Field } from "formik";
import InputSelect from "../../components/formikForm/InputSelect";
import { AppHeading } from "../../components/AppComponents";
import InputGroup from "../../components/formikForm/InputGroup";
import InputLabel from "../../components/form/InputLabel";

const IndividualProduct = ({ values, setFieldValue }) => {
  const subProduct = [
    { value: "DOC", label: "Doctor Consult" },
    { value: "AC", label: "Accidental" },
    { value: "HCH", label: "HospitalCash" },
  ];

  return (
    <>
      <div>
        <p className="text-lg mt-3">Specification</p>
        <hr />
        <div
          role="group"
          aria-labelledby="my-radio-group"
          className="flex justify-between mt-4"
        >
          {subProduct.map((item, index) => (
            <div key={index} className="mt-2">
              <div className="flex">
                <Field
                  type="radio"
                  name="specification"
                  value={item.value}
                  className="mr-3 cursor-pointer"
                />
                <InputLabel label={item.label} />
              </div>
              {values.specification === item.value
                ? item.value != "DOC" && (
                    <div className="mt-3">
                      <InputGroup
                        label="Claim Amount"
                        name="claimAmount"
                        requiredMark="true"
                        placeholder=""
                        placeHolder="0"
                        type="number"
                      />
                      <div className="text-xs text-red-500">
                        <ErrorMessage name="claimAmount" />
                      </div>
                    </div>
                  )
                : false}
            </div>
          ))}
        </div>
      </div>

      {values.specification === "HCH" ? (
        <>
          <AppHeading className="mt-6">Hospital Slab</AppHeading>
          <hr />
          <div className="mt-4 grid lg:grid-cols-4 sm:grid-cols-1 md:grid-cols-3 gap-4">
            <div className="flex">
              <Field
                type="checkbox"
                name="isHosSlab1"
                className="mr-3 cursor-pointer"
              />
              <InputLabel label="Hospital Slab 1" />
            </div>
            {values.isHosSlab1 ? (
              <>
                <div>
                  <InputSelect
                    requiredMark="true"
                    label="Days"
                    name="hospitalSlab1.days"
                    options={[
                      { value: "1-10", label: "1-10" },
                      { value: "1-15", label: "1-15" },
                      { value: "1-20", label: "1-20" },
                      { value: "1-25", label: "1-25" },
                      { value: "1-30", label: "1-30" },
                    ]}
                  />
                  <div className="text-xs text-red-500">
                    <ErrorMessage name="hospitalSlab1.days" />
                  </div>
                </div>
                <div>
                  <InputGroup
                    label="Amount"
                    name="hospitalSlab1.amount"
                    requiredMark="true"
                    placeholder="0"
                    type="number"
                  />
                  <div className="text-xs text-red-500">
                    <ErrorMessage name="hospitalSlab1.amount" />
                  </div>
                </div>
              </>
            ) : (
              ""
            )}
          </div>
          {values.isHosSlab1 ? (
            <div className="mt-4 grid lg:grid-cols-4 sm:grid-cols-1 md:grid-cols-3 gap-4">
              <div className="flex">
                <Field
                  type="checkbox"
                  className="mr-3 cursor-pointer"
                  name="isHosSlab2"
                />
                <InputLabel label="Hospital Slab 2" />
              </div>

              {values.isHosSlab2 ? (
                <>
                  <div>
                    <InputSelect
                      requiredMark="true"
                      label="Days"
                      name="hospitalSlab2.days"
                      options={[
                        { value: "1-10", label: "1-10" },
                        { value: "1-15", label: "1-15" },
                        { value: "1-20", label: "1-20" },
                        { value: "1-25", label: "1-25" },
                        { value: "1-30", label: "1-30" },
                      ]}
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="hospitalSlab2.days" />
                    </div>
                  </div>
                  <div>
                    <InputGroup
                      label="Amount"
                      name="hospitalSlab2.amount"
                      requiredMark="true"
                      placeholder="0"
                      type="number"
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="hospitalSlab2.amount" />
                    </div>
                  </div>
                </>
              ) : (
                ""
              )}
            </div>
          ) : (
            ""
          )}
          {values.isHosSlab2 && values.isHosSlab1 ? (
            <div className="mt-4 grid lg:grid-cols-4 sm:grid-cols-1 md:grid-cols-3 gap-4">
              <div className="flex">
                <Field
                  type="checkbox"
                  className="mr-3 cursor-pointer"
                  name="isHosSlab3"
                />
                <InputLabel label="Hospital Slab 3" />
              </div>

              {values.isHosSlab3 ? (
                <>
                  <div>
                    <InputSelect
                      requiredMark="true"
                      label="Days"
                      name="hospitalSlab3.days"
                      options={[
                        { value: "1-10", label: "1-10" },
                        { value: "1-15", label: "1-15" },
                        { value: "1-20", label: "1-20" },
                        { value: "1-25", label: "1-25" },
                        { value: "1-30", label: "1-30" },
                      ]}
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="hospitalSlab3.days" />
                    </div>
                  </div>
                  <div>
                    <InputGroup
                      label="Amount"
                      name="hospitalSlab3.amount"
                      requiredMark="true"
                      placeholder="0"
                      type="number"
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="hospitalSlab3.amount" />
                    </div>
                  </div>
                </>
              ) : (
                ""
              )}
            </div>
          ) : (
            ""
          )}

          {values.isHosSlab3 && values.isHosSlab2 && values.isHosSlab1 ? (
            <div className="mt-4 grid lg:grid-cols-4 sm:grid-cols-1 md:grid-cols-3 gap-4">
              <div className="flex">
                <Field
                  type="checkbox"
                  className="mr-3 cursor-pointer"
                  name="isHosSlab4"
                />
                <InputLabel label="Hospital Slab 4" />
              </div>
              {values.isHosSlab4 ? (
                <>
                  <div>
                    <InputSelect
                      requiredMark="true"
                      label="Days"
                      name="hospitalSlab4.days"
                      options={[
                        { value: "1-10", label: "1-10" },
                        { value: "1-15", label: "1-15" },
                        { value: "1-20", label: "1-20" },
                        { value: "1-25", label: "1-25" },
                        { value: "1-30", label: "1-30" },
                      ]}
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="hospitalSlab4.days" />
                    </div>
                  </div>
                  <div>
                    <InputGroup
                      label="Amount"
                      name="hospitalSlab4.amount"
                      requiredMark="true"
                      placeholder="0"
                      type="number"
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="hospitalSlab4.amount" />
                    </div>
                  </div>
                </>
              ) : (
                ""
              )}
            </div>
          ) : (
            ""
          )}

          <div className="mt-10">
            <AppHeading className="mt-6">ICU SLAB</AppHeading>
            <hr />
            <div className="mt-4 grid lg:grid-cols-4 sm:grid-cols-1 md:grid-cols-3 gap-4">
              <div className="flex">
                <Field
                  type="checkbox"
                  className="mr-3 cursor-pointer"
                  name="isICUSlab1"
                />
                <InputLabel label="ICU Slab 1" />
              </div>
              {values.isICUSlab1 ? (
                <>
                  <div>
                    <InputSelect
                      requiredMark="true"
                      label="Days"
                      name="icuSlab1.days"
                      options={[
                        { value: "1-10", label: "1-10" },
                        { value: "1-15", label: "1-15" },
                        { value: "1-20", label: "1-20" },
                        { value: "1-25", label: "1-25" },
                        { value: "1-30", label: "1-30" },
                      ]}
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="icuSlab1.days" />
                    </div>
                  </div>

                  <div>
                    <InputGroup
                      label="Amount"
                      name="icuSlab1.amount"
                      requiredMark="true"
                      placeholder="0"
                      type="number"
                    />
                    <div className="text-xs text-red-500">
                      <ErrorMessage name="icuSlab1.amount" />
                    </div>
                  </div>
                </>
              ) : (
                ""
              )}
            </div>

            {values.isICUSlab1 ? (
              <div className="mt-4 grid lg:grid-cols-4 sm:grid-cols-1 md:grid-cols-3 gap-4">
                <div className="flex">
                  <Field
                    type="checkbox"
                    className="mr-3 cursor-pointer"
                    name="isICUSlab2"
                  />
                  <InputLabel label="ICU Slab 2" />
                </div>
                {values.isICUSlab2 ? (
                  <>
                    <div>
                      <InputSelect
                        requiredMark="true"
                        label="Days"
                        name="icuSlab2.days"
                        options={[
                          { value: "1-10", label: "1-10" },
                          { value: "1-15", label: "1-15" },
                          { value: "1-20", label: "1-20" },
                          { value: "1-25", label: "1-25" },
                          { value: "1-30", label: "1-30" },
                        ]}
                      />
                      <div className="text-xs text-red-500">
                        <ErrorMessage name="icuSlab2.days" />
                      </div>
                    </div>
                    <div>
                      <InputGroup
                        label="Amount"
                        name="icuSlab2.amount"
                        requiredMark="true"
                        placeholder="0"
                        type="number"
                      />
                      <div className="text-xs text-red-500">
                        <ErrorMessage name="icuSlab2.amount" />
                      </div>
                    </div>
                  </>
                ) : (
                  ""
                )}
              </div>
            ) : (
              ""
            )}
          </div>
        </>
      ) : (
        ""
      )}
    </>
  );
};

export default IndividualProduct;
